import React from 'react';

import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';

import '../scss/config.scss'

export default function Person() {
    return (
        <Paper className='paper' style={{ backgroundColor: 'transparent', color: 'white', }}>
            {[
                'Intro',
                '• Critical thinkers have good problem-solving skills',
                '• Actively learning, researching and seeking new technologies',
                '• Adapting quickly to a new environment',
                '• Good personal and interpersonal skills'
            ].map(t => { return <Typography key={Math.random()} variant='pre'>{t}<br /></Typography> })}
        </Paper>
    );
}
