import React from 'react';

import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';

import '../scss/config.scss'

import uitIcon from '../uit-icon.png'

export default function Education() {
    return (
        <Paper className='paper' style={{ backgroundColor: 'transparent', color: 'white', }}>
            <div style={{ display: 'flex', padding: '1%', margin: '1%', }}>
                <a href="https://www.uit.edu.vn/">
                    <img src={uitIcon}
                        style={{ width: '150px', height: '150px', objectPosition: '0', objectFit: 'cover', float: 'left' }}
                        alt="Vietnam National University - Ho Chi Minh City, University of Infomation Technology" />
                </a>
                <div>
                    <Typography variant='p'>Vietnam National University - Ho Chi Minh City</Typography><br />
                    <Typography variant='p'>University of Infomation Technology</Typography><br />
                </div>
            </div>
            {[
                '09/2017 - Present',
                'Major: Computer Science',
                'Student code: 17520490',
                'GPA: 8.02/10.0',
            ].map(t => { return <Typography key={Math.random()} variant='pre'>{t}<br /></Typography> })}
        </Paper>
    );
}
