import * as React from 'react';

import Box from '@material-ui/core/Box';
import Paper from '@material-ui/core/Paper';

import Step from '@material-ui/core/Step';
import Stepper from '@material-ui/core/Stepper';
import StepButton from '@material-ui/core/StepButton';
import StepContent from '@material-ui/core/StepContent';

import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';

import '../scss/config.scss'

const date = new Date

const steps = [
    {
        label: '7/2020',
        description: `Joined FPT Telecom`,
        details: `SCC department, team R&D, position Software Developer`,
    },
    {
        label: `Now ${date.getMonth() + 1}/${date.getFullYear()}`,
        description: `same`,
        details: `same`,
    },
];

export default function HorizontalNonLinearStepper() {
    const [activeStep, setActiveStep] = React.useState(steps.length - 2);

    const handleNext = () => setActiveStep((activeStep + 1) % steps.length);
    const handleBack = () => setActiveStep(activeStep - 1 < 0 ? steps.length - 1 : activeStep - 1);
    const handleStep = (step) => () => setActiveStep(step);

    return (
        <Paper className='paper' style={{ backgroundColor: 'transparent', color: 'white', }}>
            <Box>
                <Stepper nonLinear activeStep={activeStep} >
                    {steps.map((step, index) => (
                        <Step key={step.label} style={{ width: 'min-content', }}>
                            <StepButton color="white" onClick={handleStep(index)}>
                                <div style={{ color: 'white', fontSize: '2vw', }}>
                                    {step.label}
                                </div>
                            </StepButton>
                            <StepContent>
                                <Typography variant='p' style={{ color: 'white', fontSize: '3vw', }}>{step.description + (index === steps.length - 1 ? ' ' + steps[index - 1].label : '')}</Typography>
                            </StepContent>
                        </Step>
                    ))}
                </Stepper>

                <Paper square elevation={0} sx={{ p: 3 }} style={{ backgroundColor: 'transparent', color: 'white', fontSize: '4vw', }}>
                    <Typography variant='p'>{steps[activeStep].details + (activeStep === steps.length - 1 ? ' ' + steps[activeStep - 1].label : '')}</Typography>
                    <Box sx={{ mb: 2 }}>
                        <div>
                            <Button
                                variant="contained"
                                onClick={handleNext}
                                sx={{ mt: 1, mr: 1 }}
                            >
                                {activeStep === steps.length - 1 ? 'Finish' : 'Continue'}
                            </Button>
                            <Button
                                variant="contained"
                                onClick={handleBack}
                                sx={{ mt: 1, mr: 1 }}
                            >
                                Back
                            </Button>
                        </div>
                    </Box>
                </Paper>
            </Box>
        </Paper >
    );
}
