import * as React from 'react';

import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';

import Tab from '@material-ui/core/Tab';
import TabList from '@material-ui/lab/TabList';
import TabPanel from '@material-ui/lab/TabPanel';
import TabContext from '@material-ui/lab/TabContext';

import '../scss/config.scss'

const content = {
    'Basic Knowledge': ['Artificial Intelligence', 'Genetic algorithm', 'Machine Learning', 'Computer Vision', 'Natural Language Processing', 'Data mining', 'Web design', 'Network Security',],
    'Programming Language': ['Python', 'C/C++', 'HTML/CSS/JS', 'YAML', 'LaTeX', 'SQL', 'MDX'],
    'Frameworks, Libraries': [['Machine Learning: Scikit-learn, Tensorflow, Torch'], ['Computer Vision: OpenCV, Dlib'], ['Web: ReactJS, NodeJS, Material-UI'], ['Web-crawling: Scrapy']],
    'Tools and others': [['Git', 'Docker',], ['MS Team', 'Slack', 'Trello', 'Discord',], ['OpenVPN', 'OpenStack', 'Weka', 'Postman',]],
    'Projects': [['FPT Telecom: Gateway', 'Chatbot', 'Report', 'Kafka', 'OCR'], ['UIT: Caro', 'Augmented Reality', 'Search system', 'Build COMPILER & INTERPRETER', 'Spark', 'OpenWhisk',]],
}

export default function LabTabs() {
    const [value, setValue] = React.useState('Basic Knowledge');

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    function getStyle(isActive) {
        return isActive ? '#f00' : '#0f0'
    }

    return (
        <Paper className='paper' style={{ height: 'min(91vw,91vh)', backgroundColor: 'transparent', color: 'white', display: 'flex', }}>
            <TabContext value={value}>
                <TabList
                    centered
                    orientation="vertical"
                    onChange={handleChange}
                    TabIndicatorProps={{ style: { background: 'white', } }}
                >
                    {
                        Object.keys(content).map(key => {
                            return <Tab label={key} value={key} style={{ color: getStyle(value === key), fontSize: '2vw' }} />
                        })
                    }
                </TabList>
                {
                    Object.entries(content).map(([key, values]) =>
                        <TabPanel value={key} style={{ width: '100%' }}>
                            {
                                values.map(value =>
                                    typeof (value) == 'string' ?
                                        <Typography fontSize='max(3vh, 3vw)' fontFamily='"Font Awesome 5 Brands"'>
                                            {value}
                                        </Typography>
                                        :
                                        <TabPanel value={key} style={{ width: '100%' }}>
                                            <Typography fontSize='max(3vh, 3vw)' fontFamily='"Font Awesome 5 Brands"'>
                                                {value.toString()}
                                            </Typography>
                                        </TabPanel>
                                )
                            }
                        </TabPanel>
                    )
                }
            </TabContext>
        </Paper >
    );
}
