import * as React from 'react';
import { Link, useLocation } from 'react-router-dom';

import SpeedDial from '@material-ui/core/SpeedDial';
import SpeedDialIcon from '@material-ui/core/SpeedDialIcon';
import SpeedDialAction from '@material-ui/core/SpeedDialAction';

import MenuIcon from '@material-ui/icons/Menu';
import CloseIcon from '@material-ui/icons/Close';

import HomeIcon from '@material-ui/icons/Home';
import PersonIcon from '@material-ui/icons/Person';
import SchoolIcon from '@material-ui/icons/School';
import StorageIcon from '@material-ui/icons/Storage';
import HistoryIcon from '@material-ui/icons/History';
import ContactsIcon from '@material-ui/icons/Contacts';

import './scss/animate hovicon.scss';
import './scss/animate Effect.scss';

const withLink = (to, children) => <Link to={to} style={{ textDecoration: "none", color: "grey" }}>{children}</Link>;

const lists = {
    Contacts: <ContactsIcon style={{ color: 'white', width: '40px', height: '40px' }} />,
    Timeline: <HistoryIcon style={{ color: 'white', width: '40px', height: '40px' }} />,
    Knowledge: <StorageIcon style={{ color: 'white', width: '40px', height: '40px' }} />,
    Education: <SchoolIcon style={{ color: 'white', width: '40px', height: '40px' }} />,
    Person: <PersonIcon style={{ color: 'white', width: '40px', height: '40px' }} />,
    Home: <HomeIcon style={{ color: 'white', width: '40px', height: '40px' }} />,
}

