//     { icon: <PersonIcon style={{ color: 'white', width: '40px', height: '40px' }} />, name: 'Person' },
//     { icon: <HomeIcon style={{ color: 'white', width: '40px', height: '40px' }} />, name: 'Home' },
// ];

// for (let x in actions) actions[x].icon = withLink("/" + actions[x].name, actions[x].icon)

export default function SpeedDialTooltipOpen() {
    const [open, setOpen] = React.useState(true);
    const handleClick = () => setOpen(!open);
    const location = useLocation();

    return (
        <SpeedDial
            open={open}
            direction="left"
            ariaLabel="SpeedDial toolktip example"
            icon={<SpeedDialIcon onClick={handleClick} icon={<MenuIcon />} openIcon={<CloseIcon />} />}
            sx={{ position: 'absolute', bottom: 16, right: 16 }}
        >
            {/* {actions.map((action) => (
                <SpeedDialAction
                    FabProps={{
                        className: "hovicon small effect-8",
                        size: "small",
                        style: { backgroundColor: 'transparent', }
                    }}
                    key={action.name}
                    onClick={test(location)}
                    icon={<div className='hovicon effect-4 sub-b' style={{ color: 'white', width: '40px', height: '40px' }}>{action.icon}</div>}
                    tooltipTitle={action.name}
                />
            ))} */}

            {Object.entries(lists).map(([key, value]) => {
                return <SpeedDialAction
                    FabProps={{
                        className: `hovicon small effect-8${location.pathname.replace('/', '') == key ? ' active' : ''}`,
                        size: "small",
                        style: { backgroundColor: 'transparent', }
                    }}
                    key={key}
                    icon={withLink("/" + key, <div className={`hovicon effect-4 sub-b${location.pathname.replace('/', '') == key ? ' active' : ''}`} style={{ color: 'white', width: '40px', height: '40px' }}>{value}</div>)}
                    tooltipTitle={key}
                />
            })}
        </SpeedDial>
    );
}
