import React from 'react';

import Paper from '@material-ui/core/Paper';
import Button from '@material-ui/core/Button';
import Tooltip from '@material-ui/core/Tooltip';
import Typography from '@material-ui/core/Typography';

import LocationCityIcon from '@material-ui/icons/LocationCity';
import PhoneIcon from '@material-ui/icons/Phone';

import '../scss/config.scss'

import gmailIcon from '../mail-icon.svg'

export default function Education() {
    return (
        <Paper className='paper' style={{ backgroundColor: 'transparent', color: 'white', }}>
            <div style={{ display: 'flex', textAlign: 'center', justifyContent: 'center' }}>
                {
                    [
                        {
                            icon: <a href="https://www.facebook.com/khanhhoa.huynh.520" className="fab fa-facebook" />,
                            title1: 'Facebook',
                            title2: 'khanhhoa.huynh.520',
                        },
                        {
                            icon: <a href="https://m.me/100022064460684" className="fab fa-facebook-messenger" />,
                            title1: 'Messenger',
                            title2: 'khanhhoa.huynh.520',
                        },
                        {
                            icon: <a href="https://join.skype.com/invite/znc8JVgcAMsa" className="fab fa-skype" />,
                            title1: 'Skype',
                            title2: 'khanhhoa.huynh.520@hotmail.com',
                        },
                        {
                            icon: <a href="https://github.com/H999" className="fab fa-github" />,
                            title1: 'Github',
                            title2: 'H999',
                        },
                    ].map(e => {
                        return <Tooltip key={e.title1} title={e.title1} arrow placement="top">
                            <p>
                                <Tooltip key={e.title2} title={e.title2} arrow placement="bottom">
                                    <Button className="fab">
                                        {e.icon}
                                    </Button>
                                </Tooltip>
                            </p>
                        </Tooltip>
                    })
                }
            </div>

            {
                [
                    {
                        icon: <img src={gmailIcon} style={{ width: 'max(5vh, 5vw)', height: 'max(5vh, 5vw)', paddingRight: '1vw' }} />,
                        text: 'khanhhoa.huynh.520@gmail.com'
                    },
                    {
                        icon: <PhoneIcon style={{ width: 'max(5vh, 5vw)', height: "max(5vh, 5vw)", color: 'white', paddingRight: '1vw' }} />,
                        text: '+84 902506590'
                    },
                    {
                        icon: <LocationCityIcon style={{ width: "max(5vh, 5vw)", height: "max(5vh, 5vw)", color: 'white', paddingRight: '1vw' }} />,
                        text: 'Ho Chi Minh City, Vietnam'
                    },
                ].map(e => {
                    return <div key={Math.random()} style={{ display: 'flex', padding: '1%', margin: '1%', }}>
                        {e.icon}
                        <Typography variant='p'>{e.text}</Typography><br />
                    </div>
                })
            }
        </Paper >
    );
}
